import './App.css';

import { useState, useEffect, createContext } from 'react';
import { evaluate } from "https://esm.sh/mathjs";

const ThemeContext = createContext();


const App = () => {
  const [display, setDisplay] = useState('0');
  const [theme, setTheme] = useState(false);

  useEffect(() => {
    document.body.style.backgroundColor = theme ? '#B0A8B9' : '#F0F8FF';
  }, [theme])

  //handle numbers 
  const handleNum = (num) => {
    let tempArr = display.split(/[\+\-\*\/]/);
    if (tempArr[tempArr.length - 1] === '0' && num === '0') { return }
    else if (display === '0' && num !== '0') {
      setDisplay(num);
    } else {
      setDisplay(display + num);
    }
  }
  //handle decimal
  const handleDec = () => {
    let tempArr = display.split(/[\+\-\*\/]/);
    if (!tempArr[tempArr.length - 1].includes('.')) { setDisplay(display + '.'); }
  }
  //handle operators
  const handleOp = (ele) => {
    //dont repeat the same operator
    let lastChar = display[display.length - 1];
    if (lastChar === ele.text) { return }
    switch (ele.id) {
      case 'divide': setDisplay(display + '/');
        break;
      case 'multiply': setDisplay(display + '*');
        break;
      case 'subtract': setDisplay(display + '-');
        break;
      case 'add': setDisplay(display + '+');
        break;
      default: return console.log('err');
    }
  }
  //handle equal => result
  const handleEq = () => {
    let tempArr = display.match(/\d+|[+/*-]{1,}|./g);
    let newArr = tempArr.map(ele => {
      if (ele.match(/[+-/*]{2,}/)) {
        if (ele[ele.length - 1] === '-') {
          return ele[ele.length - 2] + ele[ele.length - 1];
        } else {
          let temp = ele[ele.length - 1];
          console.log(temp);
          return temp;
        }
      } else { return ele };
    })
    let newDisplay = newArr.join('');
    try {
      const result = evaluate(newDisplay);
      setDisplay(result.toString());
    } catch (error) {
      console.log('Error: ' + error);
    }
  }

  return (
    <>
      <ThemeContext.Provider value={{ theme, setTheme }}>
        <div className='div-main'>
          <button className="btnTheme" onClick={() => setTheme(!theme)}>Theme</button>
          <div className='div-calculator'>
            <div id='display' className={theme ? 'display display-plus' : 'display'}>{display}</div>
            <div className={theme ? 'div-buttons div-buttons-plus' : 'div-buttons'}>
              <div className='div-left'>
                <button className='button' id="clear" onClick={() => { setDisplay('0') }}>AC</button>
                {numbers.map(ele => <button className={theme ? 'button button-plus' : 'button'}
                  key={ele.id} id={ele.id}
                  onClick={() => handleNum(ele.number)}>{ele.number}</button>)}
                <button className='button' id="decimal" onClick={handleDec}>.</button>
              </div>
              <div className='div-right'>
                {operators.map(ele => <button className='button' key={ele.id} id={ele.id}
                  onClick={() => handleOp(ele)}>{ele.text}</button>)}
                <button className='button' id="equals" onClick={handleEq}>=</button>
              </div>
            </div>
          </div>
          <a id='home' href='https://yiming-liao.github.io/'>
            <div id="div-home">
              <p className={theme ? 'sig-theme sig' : 'sig'}>Yiming Liao</p>
              <i className={theme ? "fa-solid fa-house-chimney sig-theme" : "fa-solid fa-house-chimney"}></i>
            </div>
          </a>
        </div>
      </ThemeContext.Provider>
    </>
  )
}

//number set 0~9
const numbers = [
  { id: 'seven', number: '7' },
  { id: 'eight', number: '8' },
  { id: 'nine', number: '9' },
  { id: 'four', number: '4' },
  { id: 'five', number: '5' },
  { id: 'six', number: '6' },
  { id: 'one', number: '1' },
  { id: 'two', number: '2' },
  { id: 'three', number: '3' },
  { id: 'zero', number: '0' },
]

const operators = [
  { id: 'divide', text: '/' },
  { id: 'multiply', text: '*' },
  { id: 'subtract', text: '-' },
  { id: 'add', text: '+' },
]

export default App;
